	//}

	public deleteCreatureAsync = (id: number) : Promise<void> => {

        return new Promise<void>((resolve, reject) => {
            this.deleteCreature(id)
            .subscribe((res) => {
                    resolve(res);
                }, (error: string) => {
                    this._alertService.error(error);
                    resolve(null);
                });

        });
	}

	public deleteCreature = (id: number) : Observable<void> => {
        id = (id == null ? <number><any>"" : id);
        
            var _Url = `api/v${version}/Creatures/deleteCreature/${id}`;

            return this._httpClient.delete<void>(_Url)
                .pipe(
                        map((data) => {
                            
                            return data;
                        }), 
                        catchError(this.handleError)
                );
	};

    
    // Utility
    private handleError(error: HttpErrorResponse) {
        
        //debugger;
        console.debug("handleError:" + error);
        //console.error(error);
        let customError: string = "";
        if (error.message) {
            customError = error.message; // error.status === 400 ? error.error : error.statusText
        }
        return throwError(customError || 'Server error');
        //return Observable.throw(customError || 'Server error');
    }
